<template>
  <div>
    <input type="text" v-model="moneyFormat" name="" />
    <button @click="money = money * 2">*2</button>
    <div style="margin-top: 10px">
      <div>
        <label for="">password</label>
        <input type="password" v-model="password" name="" />
        <p style="margin-left: 15px; color: red">
          {{ msg.pw_validate }}
        </p>
      </div>
      <div>
        <label for="">fullname</label>
        <input type="fullname" v-model="fullname" name="" />
        <p style="margin-left: 15px; color: red">
          {{ msg.fn_validate }}
        </p>
      </div>
    </div>
    <form @submit.prevent="onSubmit">
      <input type="email" name="email" />

      <button>Sign up for newsletter</button>
      <button>Sign up for newslettersd</button>
    </form>
  </div>
</template>

<script>
export default {
  props: {
    dataTest: String,
    numCount: {
      type: Number,
      default: 100,
    },
  },
  data() {
    return { money: 0, password: "", fullname: "", msg: {} };
  },
  methods: {
    formatNumber(num) {
      return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
    },
    onSubmit() {
      alert("Submitting :(");
    },
  },
  computed: {
    moneyFormat: {
      // getter
      get() {
        return this.formatNumber(this.money);
      },
      // setter
      set(newValue) {
        this.money = parseInt(newValue.split(",").join(""));
      },
    },
  },
  watch: {
    password(val) {
      if (val.length < 8) {
        this.msg["pw_validate"] =
          "Mat khau khong duoc it hon 8, thieu " + (8 - val.length) + " ki tu";
      } else {
        this.msg["pw_validate"] = "";
      }
    },
    fullname(val) {
      var reg = /^[a-zA-Z\s]*$/;
      let res = reg.test(val);
      console.log(res);
      if (!res)
        this.msg["fn_validate"] = "Mat khau khong duoc chua ki tu dac biet";
      else this.msg["fn_validate"] = "";
    },
  },
};
</script>

