<template>
  <div style="padding-bottom: 100px">
    <h1>list of dogs</h1>
    <dog-tag :style="isLoading ? 'opacity: 0.2' : ''" :listDogs="listShow" />
    <div>
      <label for="">Posts per page</label>
      <input type="number" @change="backtotop" v-model="postsPerPage" />
    </div>
    <div style="float: right; padding: 0 50px">
      <button
        :class="paginateActive == i ? 'isActive' : ''"
        style="margin-right: 10px"
        v-for="i in getPaginateNum"
        :key="i"
        @click="changePage(i)"
      >
        {{ i }}
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import DogTag from "../components/DogTag.vue";
// import ImgLoader from "vue2-image-loader";

// import VueAxios from "vue-axios";
export default {
  mounted() {
    this.isLoading = true;
    axios.get("https://dog.ceo/api/breeds/image/random/50").then((response) => {
      this.listDogs = response.data.message;
      console.log(response.data);
      this.isLoading = false;
    });
  },
  data() {
    return {
      isLoading: false,
      listDogs: [],
      paginateActive: 1,
      postsPerPage: 5,
    };
  },
  computed: {
    getPaginateNum() {
      return Math.round(this.listDogs.length / this.postsPerPage);
    },
    listShow() {
      return this.listDogs.filter(
        (val, index) =>
          index < this.paginateActive * this.postsPerPage &&
          index >= (this.paginateActive - 1) * this.postsPerPage
      );
    },
  },
  methods: {
    changePage(val) {
      this.paginateActive = val;
    },
    backtotop() {
      this.paginateActive = 1;
    },
  },
  components: { DogTag },
};
</script>

<style scoped>
.isActive {
  background: #42b983;
  border: 2px solid #009652;
  border-radius: 1px;
  margin: 2px;
  padding: 6px 9px;
  color: white;
}
</style>